<template>
  <div class="content">
    <div class="container-fluid">
      <div class="row">
        <div class="col-12">
          <card class="strpied-tabled-with-hover"
                body-classes="table-full-width table-responsive"
          >
            <template slot="header">
              <h4 class="card-title">Portefeuille crypto</h4>
              <p class="card-category"></p>
            </template>
            <l-table class="table-hover table-striped"
                     :columns="table1.columns"
                     :data="table1.data"
                     @button-clicked="handleButtonClick">
            </l-table>
          </card>

        </div>

        <div class="col-12">
          <card class="card-plain">
            <template slot="header">
              <h4 class="card-title">Portefeuille Fiat</h4>
              <p class="card-category">Vous pouvez les echangez contre des cryptos</p>
            </template>
            <div class="table-responsive">
              <l-table class="table-hover"
                       :columns="table2.columns"
                       :data="table2.data">
              </l-table>
            </div>
          </card>
        </div>

        <div class="col-12">
          <card class="card-body">
            <template slot="header">
              <h4 class="card-title">Demande de retrait/dépôt</h4>
            </template>
            <div class="form-group">
              <label for="transactionType">Type de transaction</label>
              <select v-model="transactionType" class="form-control">
                <option value="depot">Dépôt</option>
                <option value="retrait">Retrait</option>
              </select>
            </div>
            <div class="form-group">
              <label for="amount">Quantité</label>
              <input v-model="amount" type="number" class="form-control" placeholder="Entrez la quantité">
            </div>
            <button @click="submitRequest" class="btn btn-primary">Envoyer la demande</button>
          </card>
        </div>

      </div>
    </div>
  </div>
</template>
<script>
import LTable from 'src/components/Table.vue'
import Card from 'src/components/Cards/Card.vue'

export default {
  components: {
    LTable,
    Card
  },
  data() {
    return {
      table1: {
        columns: ['Id', 'Crypto', 'Quantité'],
        data: []
      },
      table2: {
        columns: ['Id', 'Devise', 'Quantité'],
        data: []
      }
    };
  },
  mounted() {
    const token = localStorage.getItem("auth_token");
    fetch('http://localhost:8081/portefeuille', {
      method: 'GET',
      headers: {
        'Authorization': `Bearer ${token}`,
      }
    })
    .then(response => response.json())
    .then(data => {
      this.table1.data = data.portefeuille_crypto.map(p => ({
        id: p.id,
        Crypto: p.cryptomonnaie.abrev,
        Quantité: p.quantite
      }));

      this.table2.data = data.portefeuille_fiat.map(p => ({
        id: p.id,
        Devise: 'USD', // Remplace par la bonne devise si elle est disponible
        Quantité: p.quantite
      }));
    })
    .catch(error => console.error('Erreur lors de la récupération des données:', error));
  }
};
</script>

